<template>
  <div>
    <Header/>
    <el-container class="main-container">
      <el-main>
        <div class="token-title">
          <el-row>
            <el-col>
              <label for="">{{ $t('lang.token_title1') }}</label>
              <p>
                <span v-if="img !== ''">{{ img }}</span>
                <el-select v-model="value" filterable :placeholder="$t('lang.token_select')" @change="selectChange">
                  <el-option
                    v-for="item in options"
                    :label="item.name"
                    :value="item.token">
                    <span>{{ item.img }}</span>{{ item.name }}
                  </el-option>
                </el-select>
              </p>
            </el-col>
            <el-col>
              <label for="">{{ $t('lang.token_title2') }}</label>
              <p style="font-size: 16px;">{{ token }}</p>
            </el-col>
            <el-col>
              <label for=""><img src="@/assets/img/assets.png" alt="">{{ $t('lang.token_title4') }}</label>
              <p>{{ tokenData.totalSupply }} {{ tokenData.symbol }}</p>
            </el-col>
            <el-col>
              <label for="">{{ $t('lang.token_title5') }}</label>
              <p style="font-size: 16px;">{{ filterDate(tokenData.timestamp) }}</p>
            </el-col>
          </el-row>
        </div>
        <div class="token-content">
          <div class="con-title">
            {{ $t('lang.token_title6') }}
          </div>
          <el-table
            :data="tableData"
            style="width: 100%;">
            <el-table-column
              prop="time"
              :label="$t('lang.token_table1')"
              width="220">
              <template slot-scope="scope">
                {{ filterDate(scope.row.time) }}
              </template>
            </el-table-column>
            <el-table-column
              prop="from"
              :label="$t('lang.token_table2')"
              width="230">
              <template slot-scope="scope">
                <router-link tag="a" :to="{path:'/address',query:{address: scope.row.from_gkc }}">{{ subStr(scope.row.from_gkc) }}
                </router-link>
              </template>
            </el-table-column>
            <el-table-column
              prop="to"
              :label="$t('lang.token_table3')"
              width="230">
              <template slot-scope="scope">
                <router-link tag="a" :to="{path:'/address',query:{address: scope.row.to_gkc }}">{{ subStr(scope.row.to_gkc) }}
                </router-link>
              </template>
            </el-table-column>
            <el-table-column
              prop="value"
              :label="$t('lang.token_table4')"
              width="180">
              <template slot-scope="scope">
                {{ scope.row.value }} {{ tokenData.symbol }}
              </template>
            </el-table-column>
            <el-table-column
              prop="txid"
              :label="$t('lang.token_table5')"
              width="210">
              <template slot-scope="scope">
                <router-link tag="a" :to="{path:'/tokendetail',query:{txid: scope.row.txid }}">{{ subStr(scope.row.txid) }}
                </router-link>
              </template>
            </el-table-column>
            <el-table-column
              prop="block"
              :label="$t('lang.token_table6')"
              width="">
              <template slot-scope="scope">
                <router-link tag="a" :to="{path:'/block',query:{height: scope.row.block }}">{{ scope.row.block }}
                </router-link>
              </template>
            </el-table-column>
          </el-table>
          <div class="block_page">
            <pageSize :page-data="pageData" :layout="`prev, pager, next,jumper`" @currentChange="currentChange"
                      @sizeChange="sizeChange"/>
          </div>
        </div>
      </el-main>
    </el-container>
    <Footer/>
  </div>
</template>

<script>
  import Header from '@/components/Header'
  import Footer from '@/components/Footer'
  import pageSize from '@/components/Page'
  import dateFns from 'date-fns'
  import Cookies from "js-cookie";

  export default {
    components: {
      Header,
      Footer,
      pageSize
    },
    data() {
      return {
        tableData: [],
        pageData: {
          currentPage: 1,
          pageSize: 10,
          total: 0
        },
        options: [],
        value: '',
        token: "",
        tokenData: {},
        img: ""
      }
    },
    computed: {},
    mounted() {
      this.getTokenList()
    },
    methods: {
      sizeChange(size) {
        this.pageData.currentPage = 1;
        this.pageData.pageSize = size
      },
      currentChange(val) {
        this.pageData.currentPage = val;
        this.getContractList()
      },
      getTokenList() {
        this.$http.get("api/contract/token").then(res => {
          if (res.data.code === 200) {
            let data = []
            for (let item of res.data.data.data) {
              item["img"] = item.name.substring(0, 1)
              data.push(item)
            }
            data.sort(function(x, y){
              return x.name.localeCompare(y.name);
            });
            this.options = data
            if (Cookies.get("tokenpc") !== undefined) {
              this.token = Cookies.get("tokenpc");
              this.img = Cookies.get("tokenpcimg");
              this.value = Cookies.get("tokenpcname");
            } else{
              this.token = this.options[0].token;
              this.value = this.options[0].name;
              this.img = this.value.substring(0, 1)
            }

            this.getContractList()
          }
        })
      },
      getContractList() {
        this.$http.post("api/contract/list", {token: this.token, page: this.pageData.currentPage}).then(res => {
          if (res.data.code === 200) {
            this.tableData = res.data.data.data
            this.tokenData = res.data.data.token
            this.img = this.tokenData.name.substring(0,1)
            Cookies.set("tokenpcimg", this.img)
            Cookies.set("tokenpc", this.tokenData.token)
            Cookies.set("tokenpcname", this.tokenData.name)
            this.pageData.total = res.data.data.page.total
            this.pageData.pageSize = res.data.data.page.page_size
            this.pageData.currentPage = res.data.data.page.current_page
          }
        })
      },
      selectChange(val){
        this.pageData.currentPage = 1
        this.token = val
        this.getContractList()
      },
      filterDate(value, type) {
        if (!value) return ''
        if (value > 0) {
          value = value * 1000
        }
        const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
        return dateFns.format(value, filterType)
      },
      subStr(str){
        if (str === "") {
          return ""
        }
        str = str.substr(0, 12);
        str += "...";
        return str
      }
    }
  }


</script>
<style>
  .el-main {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: center;
  }

  .el-main .token-title {
    width: 1200px !important;
    min-width: 1200px;
    background-color: #ffffff;
    margin-bottom: 16px;
    border-radius:4px;
  }

  .el-main .token-title .el-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    padding: 0 26px;
  }

  .el-main .token-title .el-row .el-col {
    display: flex;
    justify-content: left;
    align-items: center;
    height: 60px;
    border-bottom: 1px solid rgba(245,245,245,1);
  }

  .el-main .token-title .el-row .el-col:last-child {
    border-left: none;
  }

  .el-main .token-title .el-row .el-col .el-select input{
    /*background-image: url("../../../static/img/token.png");*/
    /*background-repeat: no-repeat;*/
    /*background-size: 18px 18px;*/
    /*background-position-y: center;*/
    /*background-position-x: 20px;*/
    padding-left: 50px;
  }

  .el-main .token-title .el-row .el-col label {
    font-size:16px;
    color:rgba(0,0,0,1);;
    height: 35px;
    line-height: 35px;
    font-weight: 400;
    width: 25%;
    text-align: left;
  }
  .el-main .token-title .el-row .el-col label img {
    width: 26px;
    height: 26px;
    vertical-align: middle;
  }

  .el-main .token-title .el-row .el-col p {
    font-size:20px;
    color:rgba(0,0,0,1);
    height: 35px;
    line-height: 35px;
    width: 60%;
    text-align: left;
    position: relative;
  }
  .el-main .token-title .el-row .el-col p > span {
    width: 18px;
    height: 18px;
    background-color: #0E932E;
    border-radius: 18px;
    display: inline-block;
    text-align: center;
    line-height: 18px;
    color: #ffffff;
    font-size: 12px;
    position: absolute;
    z-index: 10;
    top: 11px;
    left: 20px;
  }

  .el-select-dropdown span{
    width: 18px;
    height: 18px;
    background-color: #0E932E;
    border-radius: 18px;
    display: inline-block;
    text-align: center;
    line-height: 18px;
    color: #ffffff;
    font-size: 12px;
    margin-right: 12px;
  }
  .el-select-dropdown li{
    font-size:16px;
    color:rgba(0,0,0,1);
    opacity:1;
    vertical-align: middle;
  }

  .el-main .token-content {
    width: 1200px !important;
    min-width: 1200px;
  }

  .el-main .token-content .con-title {
    height: 60px;
    background: rgba(243, 244, 247, 1);
    border-radius: 4px 4px 0 0;
    font-size: 18px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
    line-height: 18px;
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 0 27px;
  }

  .el-main .token-content .el-table {
    padding: 0 27px;
  }

  .el-main .token-content .el-table tr {
    height: 60px;
  }

  .el-main .token-content .el-table thead .cell {
    font-size: 16px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
    padding-right: 0;
  }

  .el-main .token-content .el-table tbody .cell {
    font-size: 16px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
    padding-right: 0;
  }
  .el-main .token-content .el-table tbody .cell a {
    font-size: 16px;
  }
</style>




